"use client";

import { useState, useEffect } from "react";
import VoteCard from "../components/VoteCard";
import ResultsBox from "../components/ResultsBox";
import { ethers } from "ethers";
import abi from "../utils/contractABI.json";

export default function Home() {

  const [votesA, setVotesA] = useState(0);
  const [votesB, setVotesB] = useState(0);

  const contractAddress = process.env.NEXT_PUBLIC_CONTRACT_ADDRESS;

  const getContract = () => {
    const provider = new ethers.BrowserProvider(window.ethereum);
    const signer = provider.getSigner();
    return new ethers.Contract(contractAddress, abi, signer);
  };

  const voteA = async () => {
    const contract = getContract();
    const tx = await contract.voteOptionA();
    await tx.wait();
    loadVotes();
  };

  const voteB = async () => {
    const contract = getContract();
    const tx = await contract.voteOptionB();
    await tx.wait();
    loadVotes();
  };

  const loadVotes = async () => {
    const provider = new ethers.BrowserProvider(window.ethereum);
    const contract = new ethers.Contract(contractAddress, abi, provider);

    const a = await contract.optionACount();
    const b = await contract.optionBCount();

    setVotesA(Number(a));
    setVotesB(Number(b));
  };

  useEffect(() => {
    loadVotes();
  }, []);

  return (
    <div className="min-h-screen bg-black flex flex-col justify-center items-center text-white gap-10 px-4">

      <h1 className="text-5xl font-extrabold fade-in mb-4 
                    bg-gradient-to-r from-[#00FFA3] to-[#00D1FF] bg-clip-text text-transparent">
        Which option do you support?
      </h1>

      <VoteCard onVoteA={voteA} onVoteB={voteB} />

      <ResultsBox a={votesA} b={votesB} />

    </div>
  );
}
